const actions = {
  getGeneralLedgers: () => ({
    type: "GET_GENERAL_LEDGERS_REQUEST"
  }),
  updateGeneralLedger: data => ({
    type: "UPDATE_GENERAL_LEDGER_REQUEST",
    payload: {
      body: data
    }
  }),
  // deactivateGeneralLedger: id_general_ledger => ({
  //   type: "DEACTIVATE_GENERAL_LEDGER_REQUEST",
  //   payload: { id_general_ledger }
  // }),
  // activateGeneralLedger: id_general_ledger => ({
  //   type: "ACTIVATE_GENERAL_LEDGER_REQUEST",
  //   payload: { id_general_ledger }
  // }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
