import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Row, Col, Select, Spin } from "antd";
import actions from "../../../../redux/generalLedger/generalLedger/actions";

const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class GeneralLedgerEquityForm extends Component {

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateGeneralLedger } = this.props;

        let values = [
          {
            gl_configuration_name: "service_charge",
            id_account: value.service_charge
          },
          {
            gl_configuration_name: "pos_rounding_gain",
            id_account: value.pos_rounding_gain
          },
          {
            gl_configuration_name: "pos_rounding_loss",
            id_account: value.pos_rounding_loss
          },
          {
            gl_configuration_name: "interbank_transfer",
            id_account: value.interbank_transfer
          },
        ]
        // console.log(values);
        updateGeneralLedger(values);
      }
    });
  };


  render() {
   // console.log(this.props.generalLedger);
    const { getFieldDecorator } = this.props.form;
    const { generalLedger } = this.props;
    const { loading, loadingSubmit } = generalLedger;
    const data = generalLedger.general_ledgers ? generalLedger.general_ledgers : [];

    // const used = data.is_used;
    // const error = generalLedger.errorField ? generalLedger.errorField : [];
    let service_charge=null;
    let pos_rounding_gain=null;
    let pos_rounding_loss=null;
    let interbank_transfer=null;

    // console.log(data);

    data.map((data) => {
      if(data.gl_configuration_name === "service_charge"){
        service_charge = data.id_account
      }
      if(data.gl_configuration_name === "pos_rounding_gain"){
        pos_rounding_gain = data.id_account
      }
      if(data.gl_configuration_name === "pos_rounding_loss"){
        pos_rounding_loss = data.id_account
      }
      if(data.gl_configuration_name === "interbank_transfer"){
        interbank_transfer = data.id_account
      }
    })
    

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <div>
        {this.props.generalLedger.loading  ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (

      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="interbankTransferAccount" />}
            >
              {getFieldDecorator("interbank_transfer", {
                initialValue: interbank_transfer,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={this.props.generalLedger.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedger.accounts.map((data, key) => (
                    data.id_account_type === "02"
                      ? 
                      (<Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name +")"}
                        </Option>)
                      : null
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="serviceChargeAccount" />}
            >
              {getFieldDecorator("service_charge", {
                initialValue: service_charge,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={this.props.generalLedger.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedger.accounts.map((data, key) => (
                    data.id_account_type === "10" ||
                    data.id_account_type === "14"
                      ? 
                      (<Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name +")"}
                        </Option>)
                      : null
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="posRoundingGainAccount" />}
            >
              {getFieldDecorator("pos_rounding_gain", {
                initialValue: pos_rounding_gain,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={this.props.generalLedger.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedger.accounts.map((data, key) => (
                    data.id_account_type === "17"
                      ? 
                      (<Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name +")"}
                        </Option>)
                      : null
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="posRoundingLossAccount" />}
            >
              {getFieldDecorator("pos_rounding_loss", {
                initialValue: pos_rounding_loss,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={this.props.generalLedger.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedger.accounts.map((data, key) => (
                    data.id_account_type === "18"
                      ? 
                      (<Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name +")"}
                        </Option>)
                      : null
                  ))}
                </Select>
              )}
            </Form.Item>
          </div>
        </div>

        <Row>
          <Col>
          <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
          </Col>
        </Row>

        
      </Form>
      )}
      </div>
    );
  }
}

const WrappedGeneralLedgerEquityForm = Form.create({
  name: "general_ledger_equity"
})(GeneralLedgerEquityForm);

//export default WrappedGeneralLedgerEquityForm;

const mapStateToProps = state => {
  return { generalLedger: state.generalLedger };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedGeneralLedgerEquityForm);
