import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import OtherForm from "./updateOtherForm";

// import { Tabs } from "antd";

import notification from "../../../../components/notification";
import Button from "../../../../components/uielements/button";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Box } from "../../../App/app.style";
import actions from "../../../../redux/generalLedger/generalLedger/actions";

// const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/menus";

class UpdateGeneralLedger extends Component {
  componentDidMount = () => {
    const { getGeneralLedgers } = this.props;
    getGeneralLedgers();
  };

  render() {
    //const general_ledger = this.props.generalLedger.generalLedger ? this.props.generalLedger.generalLedger : []

    //Display error notif
    if (this.props.generalLedger.error) {
      notification("error", this.props.generalLedger.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.generalLedger.success) {
      notification("success", this.props.generalLedger.success);
      this.props.clearSuccess();
    }
    //console.log(this.props.generalLedger);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="generalLedgers.other.title" />}
            style={{ margin: 0 }}
          >
            <Button
              onClick={() => this.props.history.replace(redirectTo)}
              style={{ marginLeft: "5px" }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <OtherForm auth={this.props.auth} />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, generalLedger: state.generalLedger };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateGeneralLedger);
